@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
*{margin:0;padding:0;box-sizing:border-box}.main-window{font-family:"Montserrat",sans-serif;display:flex;flex-direction:row}.request-item{display:flex;flex-direction:row;cursor:pointer;padding-right:1rem}.request-item__active{background-color:#EBEBEB}.request-item__image{background-color:#BDBDBD;border:1px solid #BDBDBD;border-radius:50%;width:40px;height:40px;display:flex;justify-content:center;align-items:center;margin:0.5rem}.request-item__briefcase{color:#FAFAFA;font-size:1.2rem}.request-item__text{margin:0.5rem 0}.request-item__value{color:#828282}.form-component__element{font-family:"Montserrat",sans-serif}.button{color:#FAFAFA;background-color:#4452B8;cursor:pointer;outline:none;padding:10px 15px;border:1px solid #4452B8;border-radius:5px;transition:opacity 250ms}.button:hover,.button:focus{opacity:0.8}.form_hide{display:none}.loading-window{font-family:"Montserrat",sans-serif}.empty-dataset-window{font-family:"Montserrat",sans-serif}

